import AnchorPixel from '../Utils/AnchorPixel';
import React from 'react'; 

const Form = (props) => {
    return (
        <div className="section section-contacts text-center">
	        <div className="row">
	          <div className="col-md-8 ml-auto mr-auto">
	            <h2 className="text-center title">Contacteaz-ne</h2>
	            <h4 className="text-center description">Pentru a stabili o programare sau pentru orice alte nelmuriri trimite-ne formularul de mai jos i te sunm napoi n cel mai scurt timp.</h4>
	            <form className="contact-form">
	              <div className="row">
	                <div className="col-md-6">
	                  <div className="form-group">
	                    <label className="bmd-label-floating">Nume</label>
	                    <input type="email" className="form-control"/>
	                  </div>
	                </div>
	                <div className="col-md-6">
	                  <div className="form-group">
	                    <label className="bmd-label-floating">Telefon</label>
	                    <input type="email" className="form-control"/>
	                  </div>
	                </div>
	              </div>
	              <div className="form-group">
	                <label htmlFor="exampleMessage" className="bmd-label-floating">Mesaj</label>
	                <textarea type="email" className="form-control" rows="4" id="exampleMessage"></textarea>
	              </div>
	              <div className="row">
	                <div className="col-md-4 ml-auto mr-auto text-center">
	                  <button className="btn btn-rose btn-raised">Trimite</button>
	                </div>
	              </div>
	            </form>
	          </div>
	        </div>
	      </div>
    );
}

export default Form;


